<template>
  <div id="admin_tongji">
    <el-row :gutter="10">
      <el-col :lg="8" :md="8" :sm="24" :xl="8" :xs="24">
        <div id="countCollegeJoinComp"
             style=" height: 130px; background-color: #fff; padding: 10px; ">
          <div class="title">
            <span>当前所有参赛学生人数（比赛未结束）</span>
          </div>
          <div class="nextLine">
            <div class="count">
              <span
                  :class="[studentJoinCompInfo.nowCount==0?'nochange':studentJoinCompInfo.nowCount>20?'changeAlot':'change']"
                  class="title_count">{{
                  studentJoinCompInfo.nowCount
                }}</span>
            </div>
            <div class="title_change">
              <div><p>昨日<i
                  :class="[studentJoinCompInfo.yesterday==0?'nochange':studentJoinCompInfo.yesterday>5?'changeAlot':'change']"
                  class="el-icon-caret-top">{{
                  studentJoinCompInfo.yesterday
                }}</i>
              </p></div>
              <div><p>一周<i
                  :class="[studentJoinCompInfo.week==0?'nochange':studentJoinCompInfo.week>10?'changeAlot':'change']"
                  class="el-icon-caret-top">{{
                  studentJoinCompInfo.week
                }}</i>
              </p></div>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :lg="8" :md="8" :sm="24" :xl="8" :xs="24">
        <div>
          <div id="countCollegeApplyProject"
               style=" height: 130px; background-color: #fff; padding: 10px; ">
            <div class="title">
              <span>当前所有学院参加竞赛数量（比赛未结束）</span>
            </div>
            <div class="nextLine">
              <div class="count">
                <span
                    :class="[collegesJoinCompInfo.nowCount==0?'nochange':collegesJoinCompInfo.nowCount>10?'changeAlot':'change']"
                    class="title_count">{{
                    collegesJoinCompInfo.nowCount
                  }}</span>
              </div>
              <div class="title_change">
                <div><p>昨日<i
                    :class="[collegesJoinCompInfo.yesterday==0?'nochange':collegesJoinCompInfo.yesterday>5?'changeAlot':'change']"
                    class="el-icon-caret-top">{{
                    collegesJoinCompInfo.yesterday
                  }}</i>
                </p></div>
                <div><p>一周<i
                    :class="[collegesJoinCompInfo.week==0?'nochange':collegesJoinCompInfo.week>15?'changeAlot':'change']"
                    class="el-icon-caret-top">{{
                    collegesJoinCompInfo.week
                  }}</i>
                </p></div>
              </div>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :lg="8" :md="8" :sm="24" :xl="8" :xs="24">
        <div>
          <div id="countStudentJoinProject"
               style=" height: 130px; background-color: #fff; padding: 10px; ">
            <div class="title">
              <span>当前所有学院项目数量（比赛未结束）</span>
            </div>
            <div class="nextLine">
              <div class="count">
                <span
                    :class="[collegeApplyCompInfo.nowCount==0?'nochange':collegeApplyCompInfo.nowCount>10?'changeAlot':'change']"
                    class="title_count">{{
                    collegeApplyCompInfo.nowCount
                  }}</span>
              </div>
              <div class="title_change">
                <div><p>昨日<i
                    :class="[collegeApplyCompInfo.yesterday==0?'nochange':collegeApplyCompInfo.yesterday>5?'changeAlot':'change']"
                    class="el-icon-caret-top">{{
                    collegeApplyCompInfo.yesterday
                  }}</i>
                </p></div>
                <div><p>一周<i
                    :class="[collegeApplyCompInfo.week==0?'nochange':collegeApplyCompInfo.week>15?'changeAlot':'change']"
                    class="el-icon-caret-top">{{
                    collegeApplyCompInfo.week
                  }}</i>
                </p></div>
              </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>

    <!--各学院参加比赛学生人次-->
    <div class="borders" style="margin-top: 20px;">
      <el-button id="studentJoinProjectBydate" size="mini" type="primary" @click="changeStudentJoinProjectBydate">
        {{ studentJoinProjectBydate == true ? '更多年份信息' : '近三年月份信息' }}<i
          class="el-icon-arrow-right el-icon--right"></i></el-button>
      <el-row>
        <div v-if="studentJoinProjectBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24">
            <el-row style="background-color: #fff;padding: 5px">
              <el-col :lg="2" :md="2" :offset="10" :xl="2">
                <el-tag
                    effect="dark"
                    type="success">
                  选择年份
                </el-tag>
              </el-col>
              <el-col :lg="5" :md="5" :xl="5">
                <el-select v-model="allStudentJoinProjectYear" placeholder="请选择">
                  <el-option
                      v-for="item in allStudentJoinProjectYearList"
                      :key="item.year"
                      :label="item.yearArry"
                      :value="item.year">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :lg="13" :md="13" :sm="13" :xl="13" :xs="13">
            <div id="allStudentJoinProjectBydate"
                 style=" height: 410px;  padding: 20px; background-color: #fff;">

            </div>
          </el-col>
          <el-col :lg="11" :md="11" :sm="11" :xl="11" :xs="11">
            <div id="allStudentJoinProject"
                 style=" height: 410px;  padding: 20px; background-color: #fff;">
            </div>
          </el-col>
        </div>
        <div v-if="!studentJoinProjectBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24"  >
            <div id="allStudentJoinProjectMoreYear"
                 style=" height: 410px;  padding: 20px; background-color: #fff; display: block;">

            </div>
          </el-col>
        </div>
      </el-row>
    </div>

    <!--各学院参加竞赛-->
    <div class="borders" style="margin-top: 20px;">
      <el-button id="collegeJoinCompBydate" size="mini" type="primary" @click="changeCollegeJoinCompBydate">
        {{ collegeJoinCompBydate == true ? '更多年份信息' : '近三年月份信息' }}<i
          class="el-icon-arrow-right el-icon--right"></i></el-button>
      <el-row>
        <div v-if="collegeJoinCompBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24">
            <el-row style="background-color: #fff;padding: 5px">
              <el-col :lg="2" :md="2" :offset="10" :xl="2">
                <el-tag
                    effect="dark"
                    type="success">
                  选择年份
                </el-tag>
              </el-col>
              <el-col :lg="5" :md="5" :xl="5">
                <el-select v-model="allCollegeJoinCompYear" placeholder="请选择">
                  <el-option
                      v-for="item in allCollegeJoinCompYearList"
                      :key="item.year"
                      :label="item.yearArry"
                      :value="item.year">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :lg="13" :md="13" :sm="24" :xl="13" :xs="24">
            <div id="allCollegeJoinCompBydate"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">
            </div>
          </el-col>
          <el-col :lg="11" :md="11" :sm="24" :xl="11" :xs="24">
            <div id="allCollegeJoinComp"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">

            </div>
          </el-col>
        </div>
        <div v-if="!collegeJoinCompBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24"  >
            <div id="allCollegeJoinCompMoreYear"
                 style=" height: 410px;  padding: 20px; background-color: #fff; display: block;">

            </div>
          </el-col>
        </div>
      </el-row>
    </div>

    <!--各学院成立项目-->
    <div class="borders" style="margin-top: 20px;">
      <el-button id="collegeApplyProjectBydate" size="mini" type="primary" @click="changeCollegeApplyProjectBydate">
        {{ collegeApplyProjectBydate == true ? '更多年份信息' : '近三年月份信息' }}<i
          class="el-icon-arrow-right el-icon--right"></i></el-button>
      <el-row>
        <div v-if="collegeApplyProjectBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24">
            <el-row style="background-color: #fff;padding: 5px">
              <el-col :lg="2" :md="2" :offset="10" :xl="2">
                <el-tag
                    effect="dark"
                    type="success">
                  选择年份
                </el-tag>
              </el-col>
              <el-col :lg="5" :md="5" :xl="5">
                <el-select v-model="allCollegeApplyProjectYear" placeholder="请选择">
                  <el-option
                      v-for="item in allCollegeApplyProjectYearList"
                      :key="item.year"
                      :label="item.yearArry"
                      :value="item.year">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :lg="13" :md="13" :sm="24" :xl="13" :xs="24">
            <div id="allCollegeApplyProjectBydate"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">
            </div>
          </el-col>
          <el-col :lg="11" :md="11" :sm="24" :xl="11" :xs="24">
            <div id="allCollegeApplyProject"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">
            </div>
          </el-col>
        </div>
        <div v-if="!collegeApplyProjectBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24"  >
            <div id="allCollegeApplyProjectMoreYear"
                 style=" height: 410px;  padding: 20px; background-color: #fff; display: block;">

            </div>
          </el-col>
        </div>
      </el-row>
    </div>

    <!-- 各学院奖项申请   -->
    <div class="borders" style="margin-top: 20px;">
      <el-button id="collegeGetAwards" size="mini" type="primary" @click="changeCollegeGetAwardsBydate">
        {{ collegeGetAwardsBydate == true ? '更多年份信息' : '近三年月份信息' }}<i
          class="el-icon-arrow-right el-icon--right"></i></el-button>
      <el-row>
        <div v-if="collegeGetAwardsBydate">
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24">
            <el-row style="background-color: #fff;padding: 5px">
              <el-col :lg="2" :md="2" :offset="4" :xl="2">
                <el-tag
                    effect="dark"
                    type="success">
                  获奖年份
                </el-tag>
              </el-col>
              <el-col :lg="3" :md="3" :offset="0.5" :xl="3">
                <el-select v-model="allCollegeGetAwardsYear" placeholder="请选择">
                  <el-option
                      v-for="item in allCollegeGetAwardsYearList"
                      :key="item.year"
                      :label="item.yearArry"
                      :value="item.year">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :lg="2" :md="2" :offset="1" :xl="2">
                <el-tag
                    effect="dark"
                    type="primary">
                  获奖类型
                </el-tag>
              </el-col>
              <el-col :lg="2" :md="2" :xl="2">
                <el-select v-model="awardsType" placeholder="请选择">
                  <el-option
                      v-for="item in awardsTypeList"
                      :key="item.type"
                      :label="item.name"
                      :value="item.type">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :lg="2" :md="2" :offset="1" :xl="2">
                <el-tag
                    effect="dark"
                    type="warning">
                  获奖等级
                </el-tag>
              </el-col>
              <el-col :lg="2" :md="2" :xl="2">
                <el-select v-model="awardsLevel" placeholder="请选择">
                  <el-option
                      v-for="item in awardsLevelList"
                      :key="item.level"
                      :label="item.name"
                      :value="item.level">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :lg="13" :md="13" :sm="24" :xl="13" :xs="24">
            <div id="allCollegeGetAwardsBydate"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">
            </div>
          </el-col>
          <el-col :lg="11" :md="11" :sm="24" :xl="11" :xs="24">
            <div id="allCollegeGetAwards"
                 style=" height: 410px; background-color: #fff; padding: 20px; ">
            </div>
          </el-col>
        </div>
        <div v-if="!collegeGetAwardsBydate">
          <el-row>
            <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24"  >
                <el-row style="background-color: #fff;padding: 5px">
                  <el-col :lg="8" :md="8" :offset="4" :xl="8">
                    <el-checkbox-group v-model="awardsTypeArry">
                      <el-checkbox-button v-for="type in awardsTypeList" :label="type.type" :key="type.type">{{type.name}}</el-checkbox-button>
                    </el-checkbox-group>
                  </el-col>
                  <el-col :lg="8" :md="8" :offset="4" :xl="8">
                    <el-checkbox-group v-model="awardsLevelArry">
                      <el-checkbox-button v-for="level in awardsLevelList" :label="level.level" :key="level.level">{{level.name}}</el-checkbox-button>
                    </el-checkbox-group>
                  </el-col>
                </el-row>
            </el-col>
          </el-row>
          <el-row>
          <el-col :lg="24" :md="24" :sm="24" :xl="24" :xs="24">
            <div id="allCollegeGetAwardsMoreYear"
                 style=" height: 600px;  padding: 20px; background-color: #fff;">
            </div>
          </el-col>
          </el-row>
        </div>
      </el-row>
    </div>

  </div>
</template>
<script>
import api from '../../main.js'
import util from '../../util.js'
import * as echarts from 'echarts'

export default {
  name: "admin_tongji",
  data() {
    return {
      tongji: {},
      proNum: 0,
      charts: '',
      studentJoinCompInfo: {},
      collegesJoinCompInfo: {},
      collegeApplyCompInfo: {},
      collegeJoinCompInfo: {},
      pip_allCollegeJoinComp: {},
      pip_allCollegeJoinCompByDate: {},
      pip_allStudentJoinProject: {},
      pip_allStudentJoinProjectByDate: {},
      pip_allCollegeApplyProject: {},
      pip_allCollegeApplyProjectByDate: {},
      allCollegeApplyProjectYearList: [],
      allCollegeApplyProjectYear: 0,
      allCollegeJoinCompYearList: [],
      allCollegeJoinCompYear: 0,
      allStudentJoinProjectYearList: [],
      allStudentJoinProjectYear: 0,
      allCollegeGetAwardsYearList:[],
      allCollegeGetAwardsYear:0,
      studentJoinProjectBydate: true,
      collegeJoinCompBydate: true,
      collegeApplyProjectBydate: true,
      collegeGetAwardsBydate:true,
      allCollegeApplyProjectMoreYearDate: {},
      allCollegeJoinCompMoreYearDate:{},
      allStudentJoinProjectMoreYearDate:{},
      awardsTypeList:[],
      awardsLevelList:[],
      awardsType:0,
      awardsLevel:0,
      awardsTypeArry:[],
      awardsLevelArry:[],
      collegeAwardsMoreYear:{}
    }
  },
  mounted() {

    /**
     * 根据时间日期获得学院参加竞赛评比信息
     */
    this.getAllCollegeJoinCompInfoByDate(this.allCollegeJoinCompYear);

    /**
     * 学院参加竞赛评比
     */
    this.getAllCollegeJoinCompInfo(this.allCollegeJoinCompYear);

    /**
     * 各学院参加比赛学生人次
     */
    this.getAllStudentJoinProject(this.allStudentJoinProjectYear);

    /**
     * 各学院参加比赛学生人次根据日期
     */
    this.getAllStudentJoinProjectByDate(this.allStudentJoinProjectYear);

    /**
     * 各学院成立竞赛项目数
     */
    this.getAllCollegeApplyProject(this.allCollegeApplyProjectYear);

    /**
     * 各学院参加比赛项目数量根据日期
     */
    this.getAllCollegeApplyProjectByDate(this.allCollegeApplyProjectYear);

    this.getAllCollegeGetAwardsByDate(this.allCollegeGetAwardsYear,this.awardsType,this.awardsLevel);

    this.getAllCollegeGetAwards(this.allCollegeGetAwardsYear,this.awardsType,this.awardsLevel);

    this.getStudentJoinCompInfo();

    this.getCollegesJoinCompInfo();

    this.getCollegeApplyCompInfo();

    this.getTimeLine();

    this.getAwardsType();



  },
  computed:{
    listenAwards(){
      const {allCollegeGetAwardsYear,awardsType,awardsLevel} = this;
      return {allCollegeGetAwardsYear,awardsType,awardsLevel};
    },
    listenAwardsTypeAndLevel(){
      const {awardsTypeArry,awardsLevelArry} = this;
      return {awardsTypeArry,awardsLevelArry};
    }
  },
  methods: {

    /**
     * 学院参加竞赛评比
     */
    getAllCollegeJoinCompInfo(year) {
      let that = this;
      that.$axios
          .get(api + `/datas/getAllCollegeJoinCompInfo/` + year)
          .then(function (response) {
            that.pip_allCollegeJoinComp = response.data;
            //学院申请竞赛比较
            let allCollegeJoinComp = echarts.init(document.getElementById("allCollegeJoinComp"))
            allCollegeJoinComp.setOption({
              title: {
                text: '各学院参加竞赛次数(总数：' + that.pip_allCollegeJoinComp.sum + ')',
                x: 'center',//中间字的位置
                y: 'bottom',//
                orient: 'horizontal',
                textStyle: {
                  fontSize: 16,
                  fontWeight: 'bolder',
                  color: '#363636'
                },
              },
              tooltip: {
                trigger: 'item'
              },
              legend: {
                orient: 'vertical',
                icon: 'circle',
                x: 'left',//标签的位置
                y: 'top'//图的位置
              },
              series: [
                {
                  name: '各学院参加竞赛次数',
                  type: 'pie',
                  radius: '50%',
                  center: ['60%', '50%'],  // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
                  label: {
                    show: true,
                    padding: [0, -120],
                    overflow: 'none',
                    fontSize: '12',
                    fontWeight: 'bold',
                    position: 'outer',
                    formatter: '{b}\n\n{c}'
                  },

                  labelLine: {
                    show: true,
                    length: 30,
                    length2: 100
                  },
                  itemStyle: {
                    emphasis: {
                      shadowBlur: 30,
                      shadowOffsetX: 20,
                      shadowColor: 'rgba(30, 144, 255，0.5)'
                    }
                  },
                  data: that.pip_allCollegeJoinComp.collegeInfo
                }
              ]
            })

          })
    },

    /**
     * 根据时间日期获得学院参加竞赛评比信息
     */
    getAllCollegeJoinCompInfoByDate(year) {
      let that = this;
      that.$axios
          .get(api + `/datas/getAllCollegeJoinCompInfoByDate/` + year)
          .then(function (response) {
            that.pip_allCollegeJoinCompByDate = response.data;
            // 按月份统计学院申请竞赛数量
            let allCollegeJoinCompBydate = echarts.init(document.getElementById("allCollegeJoinCompBydate"))
            allCollegeJoinCompBydate.setOption({
              title: {
                text: that.pip_allCollegeJoinCompByDate.text
              },
              // 提示框
              tooltip: {
                trigger: 'axis'
              },
              // 图例
              legend: {
                icon: 'circle',
                orient: 'vertical',
                left: 'left',
                top: 'center',
                data: that.pip_allCollegeJoinCompByDate.collegeNames
              },
              grid: {
                left: '20%',
                right: '3%',
                bottom: '3%',
                containLabel: true
              },
              // 工具栏
              toolbox: {
                feature: {
                  saveAsImage: {
                    type: 'png'
                  },
                  magicType: {
                    type: ['line', 'bar', 'stack']
                  }
                }
              },
              xAxis: {
                type: 'category',
                boundaryGap: false,
                data: that.pip_allCollegeJoinCompByDate.date
              },
              yAxis: {
                type: 'value'
              },
              series: that.pip_allCollegeJoinCompByDate.collegeInfo
            })
          })
    },

    /**
     * 各学院参加比赛学生人次
     */
    getAllStudentJoinProject(year) {
      let that = this;
      that.$axios
          .get(api + `/datas/getAllStudentJoinProjectInfo/` + year)
          .then(function (response) {
            that.pip_allStudentJoinProject = response.data;
            let allStudentJoinProject = echarts.init(document.getElementById("allStudentJoinProject"))
            allStudentJoinProject.setOption({
              title: {
                text: '各学院参加竞赛学生人次(总人次：' + that.pip_allStudentJoinProject.sum + ')',
                x: 'center',//中间字的位置
                y: 'bottom',//
                orient: 'horizontal',
                textStyle: {
                  fontSize: 16,
                  fontWeight: 'bolder',
                  color: '#363636'
                },
              },
              tooltip: {
                trigger: 'item'
              },
              legend: {
                orient: 'vertical',
                icon: 'circle',
                x: 'left',//标签的位置
                y: 'top'//图的位置
              },
              series: [
                {
                  name: '院系竞赛学生人次',
                  type: 'pie',
                  radius: '50%',
                  center: ['60%', '50%'],  // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
                  label: {
                    show: true,
                    padding: [0, -120],
                    overflow: 'none',
                    fontSize: '12',
                    fontWeight: 'bold',
                    position: 'outer',
                    formatter: '{b}\n\n{c}'
                  },

                  labelLine: {
                    show: true,
                    length: 30,
                    length2: 100
                  },
                  itemStyle: {
                    emphasis: {
                      shadowBlur: 30,
                      shadowOffsetX: 20,
                      shadowColor: 'rgba(30, 144, 255，0.5)'
                    }
                  },
                  data: that.pip_allStudentJoinProject.collegeInfo
                }
              ]
            })

          })
    },

    /**
     * 各学院参加比赛学生人次根据日期
     */
    getAllStudentJoinProjectByDate(year) {
      let that = this;
      that.$axios
          .get(api + `/datas/getAllStudentJoinProjectByDate/` + year)
          .then(function (response) {
            that.pip_allStudentJoinProjectByDate = response.data;
            // 按月份统计学院申请竞赛数量
            let allStudentJoinProjectBydate = echarts.init(document.getElementById("allStudentJoinProjectBydate"))
            allStudentJoinProjectBydate.setOption({
              title: {
                text: that.pip_allStudentJoinProjectByDate.text
              },
              // 提示框
              tooltip: {
                trigger: 'axis'
              },
              // 图例
              legend: {
                icon: 'circle',
                orient: 'vertical',
                left: 'left',
                top: 'center',
                data: that.pip_allStudentJoinProjectByDate.collegeNames
              },
              grid: {
                left: '20%',
                right: '3%',
                bottom: '3%',
                containLabel: true
              },
              // 工具栏
              toolbox: {
                feature: {
                  saveAsImage: {
                    type: 'png'
                  },
                  magicType: {
                    type: ['line', 'bar', 'stack']
                  }
                }
              },
              xAxis: {
                type: 'category',
                boundaryGap: false,
