    getTimeLine() {
      let nowYear = util.getYear();//2021
      let nextYear = nowYear + 1;//2022
      let lastYear = nowYear - 1;//2020
      let beforeLastYear = lastYear - 1;//2019
      let result = [];
      result.push(
          {
            year: nowYear,
            yearArry: nowYear + '年~' + nextYear + '年'
          }
      );
      result.push(
          {
            year: lastYear,
            yearArry: lastYear + '年~' + nowYear + '年'
          }
      );
      result.push(
          {
            year: beforeLastYear,
            yearArry: beforeLastYear + '年~' + lastYear + '年'
          }
      );
      result.push(
          {
            year: 0,
            yearArry: '过去12个月'
          }
      );
      this.allCollegeApplyProjectYearList = result;
      this.allCollegeJoinCompYearList = result;
      this.allStudentJoinProjectYearList = result;
      this.allCollegeGetAwardsYearList = result;
      this.allCollegeApplyProjectYear = nowYear;
      this.allCollegeJoinCompYear = nowYear;
      this.allStudentJoinProjectYear = nowYear;
      this.allCollegeGetAwardsYear = nowYear;
    },

